.popup {
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: opacity 500ms linear;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .5);
    justify-content: center;
    align-items: center;
    padding: 0 19px;
}
